.container-box{
    width: 60%;
    padding: 50px;
    border-radius: 20px;
    box-shadow: 1px 3px 5px rgba(100, 97, 97, 0.578);

  }
.form-signin{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
 
}
footer{
  margin-top: 50px;
}
footer.shipping{
  margin-top: 550px;
}
label{
    align-self: start;
}
a{
  text-decoration: none;
  color:rgb(230, 71, 143);
  font-size:1.2rem;
}
input{
    outline: none;
    padding: 20px 40px;
    font-size: 16px;
    border: none;
    box-shadow: 5px 3px 20px 0px rgba(84, 80, 80, 0.766);
    border-radius: 20px;
  }
.form-btn {
    margin: 20px 0;
    align-self: center;
    padding: 15px 100px;
    border: 2px solid rgb(66, 183, 255);
    border-radius: 25px;
    background-color: #f0f8ff;
    color: rgb(66, 61, 61);
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    font-size:1.2rem;
    transition: 0.15s background-color linear;
    box-shadow: 3px 3px 6px #86808035;
  }
  .form-btn:hover {
    background-color: rgb(66, 183, 255);
  }
  .form-btn:active {
    transform: scale(0.9);
  }
  .form-div button{
    margin-left: 50px;
    margin-right: 30px;
    padding:10px 20px;
    background-color: rgb(237, 81, 81);
    border: rgb(227, 34, 34);
    color: white;
  }
  .password{
    position: relative;
    display: flex;
    flex-direction: column;
  }
  .toggle-password-btn{
    position: absolute;
    top: 40px;
    right: 20px;
  }
  p.forgot{
    cursor: pointer;
    color:rgb(230, 71, 143);
  }
  input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

  @media (max-width:600px){
    .container-box{
      width: 90%;
      padding: 10px;
      border-radius: 20px;
      box-shadow: 1px 3px 5px rgba(100, 97, 97, 0.578);
  
    }
    .form-btn {
      padding: 15px 50px;

    }
    .container{
      margin-top: 50px;
    }
  }